import React from 'react';
import ReactDOM from 'react-dom';
import arch from "./ready/arch.png"
import bacon from "./ready/bacon.png"
import basil from "./ready/basil.png"
import chili from "./ready/chili.png"
import mozzarella from "./ready/mozzarella.png"
import mushroom from "./ready/mushroom.png"
import olive from "./ready/olive.png"
import onion from "./ready/onion.png"
import pepper from "./ready/pepper.png"
import pepperoni from "./ready/pepperoni.png"
import tomato from "./ready/tomato.png"

class App extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            arch : false,
            bacon : false,
            bosil : false,
            chili : false,
            mozzarella : false,
            mushroom : false,
            olive : false,
            onion : false,
            pepper : false,
            pepperoni : false,
            tomato : false
        }
        this.addProduct = this.addProduct.bind(this);

    }
    addProduct(e){
        let a =e.target.value;
        console.log((a));
        if(a === 'arch')
        this.setState((prev) => ({
            arch : !prev.arch
        }));
        if(a === 'bacon')
            this.setState((prev) => ({
                bacon : !prev.bacon
            }));
        if(a === 'basil')
            this.setState((prev) => ({
                basil : !prev.basil
            }));
        if(a === 'chili')
            this.setState((prev) => ({
                chili : !prev.chili
            }));
        if(a === 'mozzarella')
            this.setState((prev) => ({
                mozzarella : !prev.mozzarella
            }));
        if(a === 'mushroom')
            this.setState((prev) => ({
                mushroom : !prev.mushroom
            }));
        if(a === 'olive')
            this.setState((prev) => ({
                olive : !prev.olive
            }));
        if(a === 'onion')
            this.setState((prev) => ({
                onion : !prev.onion
            }));
        if(a === 'pepper')
            this.setState((prev) => ({
                pepper : !prev.pepper
            }));
        if(a === 'pepperoni')
            this.setState((prev) => ({
                pepperoni : !prev.pepperoni
            }));
        if(a === 'tomato')
            this.setState((prev) => ({
                tomato : !prev.tomato
            }));
        if(a === 'all')
            this.setState({
                arch : true,
                bacon : true,
                basil : true,
                chili : true,
                mozzarella : true,
                mushroom : true,
                olive : true,
                onion : true,
                pepper : true,
                pepperoni : true,
                tomato : true
            });
        if(a === 'disAll')
            this.setState({
                arch : false,
                bacon : false,
                basil : false,
                chili : false,
                mozzarella : false,
                mushroom : false,
                olive : false,
                onion : false,
                pepper : false,
                pepperoni : false,
                tomato : false
            });


    }
    render(){
        return(
            <div>
            <div className="board">
                <div className="base">
                    <img src={arch} alt="arch" className="arch" style={{display : this.state.arch ? 'block' : 'none'}}/>
                    <img src={bacon} alt="bacon" className="bacon" style={{display : this.state.bacon ? 'block' : 'none'}}/>
                    <img src={basil} alt="basil" className="basil" style={{display : this.state.basil ? 'block' : 'none'}}/>
                    <img src={chili} alt="chili" className="chili" style={{display : this.state.chili ? 'block' : 'none'}}/>
                    <img src={mozzarella} alt="mozzarella" className="mozzarella" style={{display : this.state.mozzarella ? 'block' : 'none'}}/>
                    <img src={mushroom} alt="mushroom" className="mushroom" style={{display : this.state.mushroom ? 'block' : 'none'}}/>
                    <img src={olive} alt="olive" className="olive" style={{display : this.state.olive ? 'block' : 'none'}}/>
                    <img src={onion} alt="onion" className="onion" style={{display : this.state.onion ? 'block' : 'none'}}/>
                    <img src={pepper} alt="pepper" className="pepper" style={{display : this.state.pepper ? 'block' : 'none'}}/>
                    <img src={pepperoni} alt="pepperoni" className="pepperoni" style={{display : this.state.pepperoni ? 'block' : 'none'}}/>
                    <img src={tomato} alt="tomato" className="tomato" style={{display : this.state.tomato ? 'block' : 'none'}}/>
                </div>
            </div>
            <div className='tabs'>
                <div>
                <button value='arch' onClick={this.addProduct}>Anchovies</button>
                <button value='bacon' onClick={this.addProduct}>Bacon</button>
                <button value='basil' onClick={this.addProduct}>Basil</button>
                </div>
                <div>
                <button value='chili' onClick={this.addProduct}>Chili</button>
                <button value='mozzarella' onClick={this.addProduct}>Mozzarella</button>
                <button value='mushroom' onClick={this.addProduct}>Mushroom</button>
                </div>
                <div>
                <button value='olive' onClick={this.addProduct}>Olive</button>
                <button value='onion' onClick={this.addProduct}>Onion</button>
                <button value='pepper' onClick={this.addProduct}>Pepper</button>
                </div>
                <button value='pepperoni' onClick={this.addProduct}>Pepperoni</button>
                <button value='tomato' onClick={this.addProduct}>Tomato</button>
                <button value='all' onClick={this.addProduct}>All</button>
                <button value='disAll' onClick={this.addProduct}>DisAll</button>
            </div>
            </div>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'));




